.product {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  &__item {
    margin-bottom: 40px;
    margin-right: 16px;
    width: 272px;
    border: 1px solid rgba(180, 189, 195, 1);

    &:nth-child(4n) {
      margin-right: 0;
    }
  }
}
